.page {
  font-family: 'Manrope', Arial, sans-serif;
  background-color: #E3F4EC;
  position: relative;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
